"use client";
import { useState, useEffect } from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';

export default function Navbar({ cartCount, onOpenCart }) {
    const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
    const [isMobileCategoriesOpen, setIsMobileCategoriesOpen] = useState(false);
    const pathname = usePathname();

    // Close mobile menu when route changes
    useEffect(() => {
        setIsMobileMenuOpen(false);
    }, [pathname]);

    return (
        <header className="bg-white shadow-sm sticky top-0 z-50">
            <div className="container mx-auto px-4 py-4 flex justify-between items-center">
                <div className="flex items-center">
                    <Link href="/shop" className="text-2xl font-bold tracking-tight">
                        NOVA <span className="text-gray-800">WEAR</span>
                    </Link>
                </div>

                {/* Desktop Nav */}
                <nav className="hidden md:flex space-x-8 items-center">
                    <Link href="/shop" className="nav-link font-medium text-gray-700 hover:text-black">Home</Link>
                    <div className="relative group">
                        <button className="nav-link font-medium text-gray-700 hover:text-black flex items-center">
                            Shop <i className="fas fa-chevron-down ml-1 text-xs"></i>
                        </button>
                        <div className="absolute left-0 mt-2 w-48 bg-white shadow-lg rounded-md py-1 z-50 hidden group-hover:block fade-in">
                            <Link href="/shop" className="block px-4 py-2 hover:bg-gray-100">All Products</Link>
                            <Link href="/shop?category=hoodies" className="block px-4 py-2 hover:bg-gray-100">Hoodies</Link>
                            <Link href="/shop?category=tshirts" className="block px-4 py-2 hover:bg-gray-100">T-Shirts</Link>
                            <Link href="/shop?category=pants" className="block px-4 py-2 hover:bg-gray-100">Pants</Link>
                            <Link href="/shop?category=outerwear" className="block px-4 py-2 hover:bg-gray-100">Outerwear</Link>
                        </div>
                    </div>
                    <Link href="#about" className="nav-link font-medium text-gray-700 hover:text-black">About</Link>
                    <Link href="#contact" className="nav-link font-medium text-gray-700 hover:text-black">Contact</Link>
                </nav>

                {/* Icons */}
                <div className="flex items-center space-x-4">
                    <button className="p-2 text-gray-600 hover:text-black relative" onClick={onOpenCart}>
                        <i className="fas fa-shopping-bag"></i>
                        <span className="absolute -top-1 -right-1 bg-black text-white text-xs rounded-full h-5 w-5 flex items-center justify-center">
                            {cartCount}
                        </span>
                    </button>
                    <button className="p-2 text-gray-600 hover:text-black">
                        <i className="fas fa-user"></i>
                    </button>
                    <button
                        className="md:hidden p-2 text-gray-600 hover:text-black"
                        onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
                    >
                        <i className="fas fa-bars"></i>
                    </button>
                </div>
            </div>

            {/* Mobile Menu */}
            <div className={`md:hidden bg-white border-t border-gray-200 mobile-categories ${isMobileMenuOpen ? '' : 'hidden'}`}>
                <div className="container mx-auto px-4 py-2">
                    <nav className="flex flex-col space-y-3 py-4">
                        <Link href="/shop" className="font-medium text-gray-700 hover:text-black py-2">Home</Link>
                        <div>
                            <button
                                onClick={() => setIsMobileCategoriesOpen(!isMobileCategoriesOpen)}
                                className="flex justify-between items-center w-full font-medium text-gray-700 hover:text-black py-2"
                            >
                                Shop <i className="fas fa-chevron-down"></i>
                            </button>
                            <div className={`pl-4 border-l border-gray-200 mt-2 ${isMobileCategoriesOpen ? '' : 'hidden'}`}>
                                <Link href="/shop" className="block py-2 text-gray-600 hover:text-black">All Products</Link>
                                <Link href="/shop?category=hoodies" className="block py-2 text-gray-600 hover:text-black">Hoodies</Link>
                                <Link href="/shop?category=tshirts" className="block py-2 text-gray-600 hover:text-black">T-Shirts</Link>
                                <Link href="/shop?category=pants" className="block py-2 text-gray-600 hover:text-black">Pants</Link>
                                <Link href="/shop?category=outerwear" className="block py-2 text-gray-600 hover:text-black">Outerwear</Link>
                            </div>
                        </div>
                        <Link href="#about" className="font-medium text-gray-700 hover:text-black py-2">About</Link>
                        <Link href="#contact" className="font-medium text-gray-700 hover:text-black py-2">Contact</Link>
                    </nav>
                </div>
            </div>
        </header>
    );
}
